.mainBar{
    flex: 0.8;
    width: 100%;
    height: 85vh;
    overflow: scroll;
}
.mainBar::-webkit-scrollbar {
    display: none;
  }
.active{
    background-color: rgba(0,0,0);
}
.mainBar__header{
    display: flex;
    width: 100%;
    height: 70px;
    justify-content: space-between;
    margin: 0 auto;
    padding: 20px 20px;
    position: sticky;
    top: 0;
    z-index: 100;
}
.mainBar__Header_left{
    display: flex;
    justify-content: space-between;
    width: 70px;
}
.mainBar__Header_right{
    display: flex;
    align-items: center;
}
.mainBar__Header_right > button{
    font-weight: bolder;
    color: #ffffff;
    background-color: rgba(0,0,0,.8);
    border: 1px solid #ffffff;
    border-radius: 45px;
    padding: 10px 40px;
    margin-right: 20px;
    outline: none;
}
.header__profile{
    display: flex;
    align-items: center;
    border-radius: 45px;
    padding: 5px;
}
.header__profile > p{
    margin-left: 10px;
}
.header__profile:hover{
    background-color: rgba(255, 255, 255, 0.2);
}
.header__profile__avatar{
    width: 30px !important;
    height: 30px !important;
}
.mainBar__Header_left__icon{
    background-color: black !important;
    border-radius: 50% !important;
    width: 30px !important;
    height: 30px !important;
    padding: 5px !important; 
}