.musicController{
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 15vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    color: #b3b3b3;
    border-top: 1px solid #000;
}
.details__logo{
    display: flex;
}
.details__logo > p{
    color: #ffffff;
    font-weight: bolder;
    margin-right: 20px;
    margin-bottom: 10px;
    cursor: pointer;
}
.details__logo > p:hover{
    text-decoration: underline
}
.logo{
    width: 20px !important;
    height: 20px !important;
    cursor: pointer;
}
.musicController__details > p{
    cursor: pointer;
}
.icon{
    width: 30px !important;
    height: 30px !important;
}
.icon__play{
    width: 40px !important;
    height: 40px !important;
}
.musicController__details > p:hover{
    text-decoration: underline;
}

.musicController__controllers{
    width: 600px;
}
.controllers__details{
    width: 300px;
    margin: 0 auto;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
}
.controllers__Bar{
    display: flex;
    flex-direction: row;
}
.controllers__Bar > progress{
    margin: 0 20px;
    width: 500px;
}
.musicController__volume{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 200px;
    cursor: pointer;
}
.musicController__volume > progress{
    width: 100px;
}
.volume__icon{
    width: 17px !important;
    height: 17px !important;
}