.header{
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    padding: 20px 20px;
}
.header > h3{
    font-weight: bolder;
    font-size: 25px;
}
.header > p{
    color: #b3b3b3;
    font-size: 13px;
}
.header > h3:hover{
    text-decoration: underline;
}
.header > p:hover{
    text-decoration: underline;
}