.sideBar{
    flex: 0.18;
    height: 85vh;
    /* border: 1px solid red; */
    background-color: rgba(0,0,0,.8);
    padding: 20px 20px;
}
.sideBar > img{
    width: 131px;
    height: 40px;
    fill: #ffffff !important;
    margin-bottom: 30px;
}
.sideBar > h4 {
    margin-top: 20px;
    margin-bottom: 20px;
}
.sideBar > hr {
    height: 1px;
    border: 0;
    background-color: rgba(255, 255, 255, 0.2);
    margin-top: 10px;
    margin-bottom: 10px;
}