.musicContainer{
    background-color: rgba(255, 255, 255, 0.1);
    height: 241.66px;
    width: 167.66px;
    padding: 15px;
    border-radius: 10px;
    margin-right: 20px;
    position: relative;
}
.musicContainer__image > img{
    width: 127.66px;
    height: 127.66px;
    border-radius: 10px;
    margin-bottom: 10px;
    margin-top: 10px;
}
.musicContainer__title{
    font-size: 13px;
    font-weight: bolder;
    margin-bottom: 7px;
}
.musicContainer__subInfo{
    font-size: 11px;
    color: #b3b3b3;
}
.musicContainer__icon{
    position: absolute;
    bottom: 10px;
    right: 10px;
    width: 50px !important;
    height: 50px !important;
    z-index: 100;
    color: green !important;
    visibility: hidden;
    transition: 0.1s ease-in-out;
}
.musicContainer:hover > .musicContainer__icon{
    visibility: visible;
}